/* Container taking up the full height of the viewport */
.dashboard-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
}

/* Top section: 5% of the height with #206f9d color and horizontal flex alignment */
.top-section {
    background-color: #206f9d;
    height: 10%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    /* Padding for spacing from edges */
    color: white;
    font-size: 1.2rem;
    /* Adjust text size */
}

/* Bottom section: 95% of the height with #38b6ff color */
.bottom-section {
    background-color: #38b6ff;
    height: 95%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    color: rgb(255, 255, 255);
}
.background-section {
    flex-grow: 1; /* Allow this section to take the remaining space */
    display: flex; /* Use flexbox to center content */
    justify-content: center; /* Center content horizontally */
    align-items: center; /* Center content vertically */
    background-color: #38b6ff; /* Set your blue background */
  }
  

/* Left content - Welcome user */
.left-content {
    /* Aligns to the left by default with Flexbox */
}

/* Right content - Pascual Creative */
.right-content {
    /* Aligns to the right by default due to justify-content: space-between */
}

/* Project card container */
.project-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    cursor: pointer;
    padding: 20px;
    /* background-color: #ffffff50; */
    /* Slightly transparent background for the card */
    border-radius: 10px;
    transition: transform 0.2s ease-in-out;
}

/* Add hover effect to cards */
.project-card:hover {
    transform: scale(1.05);
    /* Slight zoom effect on hover */
}

/* Image styling for the project cards */
.project-card img {
    width: 150px;
    /* Adjust the image width */
    height: 150px;
    /* Adjust the image height */
    object-fit: cover;
    /* Ensure the image is properly contained */
    margin-bottom: 10px;
    border-radius: 8px;
    /* Optional: rounded corners for images */
}

/* Label styling for the cards */
.project-card p {
    margin: 0;
    font-size: 1rem;
    font-weight: bold;
    color: white;
}

.back-button {
    margin-left: 10px; /* Space between the welcome text and the button */
    padding: 5px 10px; /* Padding for the button */
    background-color: #007bff; /* Example background color */
    color: white; /* Text color */
    border: none; /* Remove border */
    border-radius: 5px; /* Rounded corners */
    cursor: pointer; /* Pointer cursor on hover */
  }
  
  .back-button:hover {
    background-color: #0056b3; /* Darker shade on hover */
  }